import React, { Component } from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import './About.scss';

class About extends Component {
    render() {
        return (
            <div id="twrap">
                <div className="wrap">
                    <Header />
                    <div className="about-box">
                        <h1>About Us</h1>
                        <img src="https://upload.wikimedia.org/wikipedia/commons/thumb/4/4e/Logo-YTS.svg/250px-Logo-YTS.svg.png" alt="logo"></img>
                        <p>YIFY Torrents or YTS was a peer-to-peer release group known for distributing large numbers of movies as free downloads through BitTorrent (see copyright infringement). YIFY releases were characterised through their consistent HD video quality in a small file size, which attracted many downloaders.</p>
                        <p>The original YIFY/YTS website was shut down by the Motion Picture Association of America (MPAA) in 2015; however, numerous websites imitating the YIFY/YTS brand still receive a significant amount of traffic. The name "YIFY" is derived from the name of the website's New Zealand founder, Yiftach Swery.</p>
                    </div>
                    <Footer />
                </div>
            </div>
        );
    }
}

export default About;