import React, { Component } from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import './Detail.scss';

class Detail extends Component {
    render() {
        const { location } = this.props;
        return (
            <div id="twrap">
                <div className="wrap">
                    <Header />
                    <div className="detail-box">
                        <div><img src={location.state.poster} alt={location.state.title} /></div>
                        <div>
                            <p className="dtit">{location.state.title}</p>
                            <p className="dyear">{location.state.year}</p>
                            <ul className="dgenre">
                        {
                            location.state.genres.map(
                            (data, i) => (<li key={i}>{data}</li>)
                            )
                        }
                    </ul>
                            <p className="dsummary">{location.state.summary}</p>
                        </div>
                    </div>
                    <Footer />
                </div>
            </div>
        );
    }
}

export default Detail;
